body {
    font-family: "Open Sans",sans-serif !important;
    color: #333;
    font-size: 16px;
}

#app {
    min-height: 100vh;
    margin: 0 auto;
}

h1,h2,h3,h4,h5,p {
    overflow-wrap: break-word;
    word-break: break-word;
    color: #333;
}

h1 {
    font-size: 2.75rem;
    font-weight: 600;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.27;
    padding: 0 0 32px 0;
    letter-spacing: 0;
    margin-bottom: 0;
}

h2 {
    font-size: 2rem;
    font-weight: 600;
    line-height: 1.25;
    padding: 24px 0 32px 0;
    letter-spacing: 0;
    font-style: normal;
    font-stretch: normal;
    margin-bottom: 0;
}

h3 {
    font-size: 1.3125rem;
    font-weight: 600;
    line-height: 1.33;
    padding: 16px 0 32px 0;
    font-style: normal;
    font-stretch: normal;
    letter-spacing: 0;
    margin-bottom: 0;
}

h4 {
    font-size: 1.125rem;
    font-weight: 600;
    line-height: 1.56;
    padding: 16px 0 16px 0;
    font-style: normal;
    font-stretch: normal;
    letter-spacing: 0;
    margin-bottom: 0;
}

h5 {
    font-size: 1rem;
    font-weight: 600;
    line-height: 1.75;
    padding: 0 0 16px 0;
    font-style: normal;
    font-stretch: normal;
    letter-spacing: 0;
    margin-bottom: 0;
}

input {
    border-radius: 0;
}

p {
    font-size: 16px;
    line-height: 1.78;
    padding: 0 0 32px 0;
    letter-spacing: .0125rem;
    text-align: left;
    margin: 0;
}

li {
    padding: 0 0 16px 0;
}

ul {
    padding: 0 0 16px 0;
    margin: 0;
}
.main-section ul {
    margin-left: 0;
    padding-left: 2rem;
    font-size: 1rem;
    line-height: 1.78;
    list-style: none;

    li {
        &:before {
            content: "";
            height: 6px;
            width: 6px;
            vertical-align: middle;
            background: #C18100;
            display: inline-block;
            border-radius: 50%;
            margin-right: 1rem;
        }
    }
}
.main-section a {
    font-weight: bold;
    color: #254f90;
    border: .0625rem dotted transparent;
    text-decoration: none;
    display: inline;
    background-color: transparent;
    -webkit-text-decoration-skip: objects;
    border-bottom: #748fb8 solid 2px;
    transition: box-shadow 250ms;
    overflow-wrap: break-word;
    word-break: break-word;

    &:hover {
        box-shadow: inset 0 -20px 0 #def;
        text-decoration: none;
    }
    &:visited {
        color: #a42079;
        border-bottom: #dfb1d0 solid 2px;
    }
}

hr {
    padding: 0 0 24px 0;
}

.padding-none {
    padding: 0;
}

.page-edit-mode {
    padding: 20px !important;
}

.padding-left-right-none {
    padding-left: 0 !important;
    padding-right: 0 !important;
}

.right-to-left div,.right-to-left p,.right-to-left h1,.right-to-left h2,.right-to-left h3,.right-to-left h4,.right-to-left h5,.right-to-left h6,.right-to-left ul,.right-to-left li {
    direction: rtl;
    text-align: right !important;
}

.divheight {
    height: 100% !important;
}

@media(max-width: 576px) {
    .padding-none-mobile {
        padding:0;
    }
}

@media(max-width: 576px) {
    h1 {
        font-size:2rem;
        line-height: 1.14;
        letter-spacing: normal;
    }

    h2 {
        font-size: 1.5rem;
        line-height: 1.33;
        letter-spacing: normal;
        font-weight: 600;
    }

    h3 {
        font-size: 1.3125rem;
        line-height: 1.875rem;
        letter-spacing: normal;
        font-weight: bold;
    }

    h4 {
        font-size: 1.125rem;
        line-height: 1.33;
    }

    h5 {
        font-size: 1rem;
    }

    p {
        font-size: 1rem;
        line-height: 1.71;
    }
}

@keyframes toRightFromLeft {
    30% {
        transform: translate(30%);
    }

    31% {
        opacity: 0;
        transform: translate(-50%);
    }

    100% {
        opacity: 1;
    }
}

@keyframes toLeftFromRight {
    30% {
        transform: translate(-30%);
    }

    31% {
        opacity: 0;
        transform: translate(50%);
    }

    100% {
        opacity: 1;
    }
}

@keyframes toTopFromBottom {
    30% {
        transform: translate(0,100%);
    }

    31% {
        opacity: 0;
        transform: translate(0,-80%);
    }

    100% {
        opacity: 1;
    }
}

@keyframes toBottomFromTop {
    30% {
        transform: translate(0,-100%);
    }

    31% {
        opacity: 0;
        transform: translate(0,80%);
    }

    100% {
        opacity: 1;
    }
}

@keyframes fadeInTop {
    0% {
        opacity: 0;
        transform: translate(0,20%);
    }

    100% {
        opacity: 1;
    }
}

@keyframes fadeOutBottom {
    0% {
        opacity: 1;
    }

    100% {
        opacity: 0;
        transform: translate(0,20%);
    }
}

@keyframes upSideDown {
    50% {
        opacity: 1;
        transform: scaleY(-.5);
    }

    100% {
        opacity: 1;
        transform: scaleY(-1);
    }
}

.link-demo a {
    font-weight: bold;
    color: #254f90;
    border: .0625rem dotted transparent;
    text-decoration: none;
    display: inline;
    background-color: transparent;
    -webkit-text-decoration-skip: objects;
    border-bottom: #748fb8 solid 2px;
    transition: box-shadow 250ms;
    overflow-wrap: break-word;
    word-break: break-word;
}

.link-demo a span {
    text-decoration: none !important;
}

.link-demo a:visited {
    color: #a42079;
    border-bottom: #dfb1d0 solid 2px;
}

.link-demo a:hover {
    box-shadow: inset 0 -20px 0 #def;
    text-decoration: none;
}

.link-demo a:hover span {
    text-decoration: none !important;
}

.link-demo a:not([href*="mailto"])[href*="//"]:after {
    padding-top: 0;
    padding-left: .25em;
    padding-bottom: .25em;
    padding-right: .25em;
    display: inline-block;
    content: url("../assets/externalLink.svg");
    height: 20px;
    width: 20px;
}

.link-demo a:not([href*="mailto"])[href*="//"]:after svg {
    fill: #4267b2;
}

.link-demo a:active {
    color: #000;
}

.link-demo a:focus {
    border: .0625rem dotted #748fb8;
    outline: none;
}

.link-demo a:not([href]):not([tabindex]) {
    border-bottom: none;
    box-shadow: none;
    color: inherit;
    text-decoration: none;
}

.link-demo a:not([href]):not([tabindex]):after {
    content: none;
}

.link-demo a:not([href]):not([tabindex]):focus {
    outline: 0;
}

.form-wrapper {
    max-width: 1140px;
    margin: 0 auto;
}

.form-wrapper h2 {
    padding-top: 24px;
    padding-bottom: 32px;
}

.form-wrapper p {
    padding-bottom: 0 !important;
}

.form-wrapper .hiding {
    display: none !important;
}

.form-wrapper .sc-formdesign-container.hiding {
    display: block;
}

.form-wrapper .fullsection {
    width: 100%;
    display: flex;
}

.form-wrapper .row {
    max-width: 82%;
    margin: 0 auto;
}

.form-wrapper input,.form-wrapper textarea {
    border: solid 1px #495c74;
    padding: 10px;
}

.form-wrapper input:focus,.form-wrapper textarea:focus,.form-wrapper select:focus {
    outline: none;
    border: solid 3px #254f90;
}

.form-wrapper .error-border {
    outline: none;
    border: solid 3px #d2201e;
}

.form-wrapper .form-text-input {
    min-height: 54px;
}

.form-wrapper span {
    color: #d2201e;
}

.form-wrapper select {
    width: 100%;
    border: solid 1px #495c74;
    padding: 10px 40px 10px 10px;
    -webkit-appearance: none;
    -moz-appearance: none;
    background-image: url("../assets/arrow-down.svg");
    background-repeat: no-repeat;
    background-position-x: 98%;
    background-position-y: center;
}

.form-wrapper select::-ms-expand {
    display: none;
}

.form-wrapper .form-text-input-label {
    font-size: 1rem;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 28px;
    letter-spacing: .2px;
    color: #333;
}

.form-wrapper .form-text-input-label::after {
    content: "*";
    color: #d2201e;
}

.form-wrapper .form-text-input-label-non-mandatory {
    font-size: 1rem;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 28px;
    letter-spacing: .2px;
    color: #333;
}

.form-wrapper .form-radio-group {
    position: relative;
    padding: 10px 15px;
}

.form-wrapper .form-radio-group input {
    display: inline-block;
    position: absolute;
    left: 15px;
    height: 24px;
    width: 24px;
}

.form-wrapper .form-radio-group input[type=radio] {
    border-radius: 100%;
}

.form-wrapper .form-radio-group input[type=radio]:checked {
    outline: none;
    color: #254f90;
    border: solid 3px #254f90;
}

.form-wrapper .form-radio-group label {
    display: block;
    position: relative;
    padding-left: 45px;
    font-weight: normal;
    border: solid 1px #495c74;
}

.form-wrapper .form-radio-group .radio-focus {
    outline: none;
    border: solid 3px #254f90;
    font-weight: 600;
    color: #254f90;
}

.form-wrapper .form-radio-group p {
    font-size: 16px;
    padding-bottom: 16px !important;
}

.form-wrapper .form-single-line-field,.form-wrapper .form-multiple-line-text {
    margin-top: 2rem;
    margin-bottom: 0;
}

.form-wrapper .form-single-line-field label,.form-wrapper .form-multiple-line-text label {
    display: block;
}

.form-wrapper .form-single-line-field input,.form-wrapper .form-multiple-line-text input {
    display: block;
    min-height: 52px;
    width: 100%;
}

.form-wrapper .form-single-line-field textarea,.form-wrapper .form-multiple-line-text textarea {
    width: 100%;
    margin-top: 0;
}

.form-wrapper .form-single-line-field input::-webkit-outer-spin-button,.form-wrapper .form-single-line-field input::-webkit-inner-spin-button,.form-wrapper .form-multiple-line-text input::-webkit-outer-spin-button,.form-wrapper .form-multiple-line-text input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.form-wrapper .form-single-line-field input[type=number],.form-wrapper .form-multiple-line-text input[type=number] {
    -moz-appearance: textfield;
}

.form-wrapper .form-notes {
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 24px;
    letter-spacing: normal;
    color: #666;
}

.form-wrapper .mb-6 {
    margin-bottom: 24px;
}

.form-wrapper .form-button {
    border: solid 2px #254f90;
    border-radius: 6px;
    padding: 8px 32px;
    text-align: center;
    color: #fff;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    font-family: "Open Sans",sans-serif;
    line-height: 24px;
    background: #4569a0;
    box-shadow: rgba(0,0,0,.1) 0 2px 4px 0;
    transition-property: transform,-webkit-transform;
    transition-duration: .4s,.4s;
    transition-timing-function: ease,ease;
    transition-delay: 0s,0s;
}

.form-wrapper .form-button:hover {
    cursor: pointer;
    border: 2px solid #18335c;
    background: #254f90;
    box-shadow: rgba(0,0,0,.3) 0 4px 8px 0;
    transform: translateY(-.1875rem);
}

.form-wrapper .form-button:disabled {
    border: 2px solid #c7c7c7;
    text-align: center;
    background: #fff;
    box-shadow: none;
    color: #8e8e8e;
}

@media(min-width: 768px) {
    .col-md-6 {
        max-width:100%;
    }

    input[type=radio] {
        border-radius: 100%;
    }
}

@media(max-width: 576px) {
    .form-wrapper {
        max-width:35rem;
    }

    .form-wrapper .row {
        max-width: 100%;
    }

    .form-wrapper input[type=radio] {
        border-radius: 100%;
    }
}

@media print {
    .hide-on-print {
        display: none !important;
    }

    body {
        -webkit-print-color-adjust: exact;
        margin-top: 1.6cm;
        margin-bottom: 2.5 cm;
    }

    .anchor-menu .sticky-container ul {
        position: relative;
    }

    .sidebar-wrapper {
        position: relative;
    }

    .sidebar-wrapper ul {
        position: relative;
    }

    .accordion-item {
        padding: 5px;
    }

    .accordion-item .accordion-item-title .accordion-item-tile-inner {
        background-color: #ebebeb !important;
    }

    .accordion-item .accordion-item-banner {
        height: 4px !important;
        background-color: #e1aa21;
        position: static !important;
    }

    .accordion-item .accordion-item-content {
        background-color: #ebebeb !important;
        height: auto !important;
        display: block !important;
    }

    .accordion-item .accordion-item-content .accordion-item-content-inner {
        display: block !important;
    }

    .accordion-item .accordion-item-content .accordion-item-content-inner .body-copy {
        -webkit-animation: none !important;
        animation: none !important;
    }

    .accordion-item .accordion-item-content .accordion-item-content-inner .body-copy p {
        -webkit-animation: none !important;
        animation: none !important;
    }

    .accordion-item .accordion-item-content .display-none {
        visibility: visible !important;
        opacity: 2 !important;
        -webkit-animation: none !important;
        animation: none !important;
    }

    .sticky-container ul {
        position: absolute !important;
        top: 0 !important;
        margin-top: 2.25rem;
        left: 0 !important;
        transform: translateZ(0);
    }
}

a.skip-main {
    left: -999px;
    position: absolute;
    top: auto;
    width: 1px;
    height: 1px;
    overflow: hidden;
    z-index: -999;
}

a.skip-main:focus,a.skip-main:active {
    color: #333;
    left: 0;
    height: auto;
    overflow: auto;
    padding: 5px;
    text-align: left;
    z-index: 999;
    position: static;
}

a.chat-now {
    position: fixed;
    display: flex;
    right: 5px;
    bottom: 5px;
    background-color: #328730;
    border: .125rem solid #134b12;
    color: #fff;
    font-size: 1rem;
    font-weight: 500;
    text-decoration: none;
    width: 160px;
    height: 48px;
    text-align: left;
    padding: 10px;
    z-index: 10;
    border-radius: 5px;
}

a.chat-now:before {
    display: inline-block;
    content: url("../assets/chatopen.svg");
    padding: 0 0 0 3px;
    height: 28px;
    width: 28px;
    margin-right: 16px;
    margin-top: -3px;
}

a.chat-now:not([href*="mailto"])[href*="//"]:after {
    content: '';
}

div.chat-later {
    position: fixed;
    display: flex;
    right: 5px;
    bottom: 5px;
    background-color: #d8d8d8;
    border: 1px solid #8e8e8e;
    color: #4c4c4c;
    font-size: 1rem;
    font-weight: 500;
    width: 160px;
    height: 48px;
    text-align: left;
    padding: 10px;
    z-index: 10;
    border-radius: 5px;
}

div.chat-later:before {
    display: inline-block;
    content: url("../assets/chatclosed.svg");
    padding: 0 0 0 3px;
    width: 28px;
    height: 28px;
    margin-right: 16px;
    margin-top: -3px;
}

div.chat-later a {
    color: #4c4c4c;
    text-decoration: none;
}

@supports (-ms-ime-align:auto) {
    div.chat-later:before {
        padding: 0;
    }

    a.chat-now:before {
        padding: 0;
    }
}

.col-5-pc {
    width: 5%;
}

.col-10-pc {
    width: 10%;
}

.col-15-pc {
    width: 15%;
}

.col-20-pc {
    width: 20%;
}

.col-25-pc {
    width: 25%;
}

.col-30-pc {
    width: 30%;
